import * as commontypes from './mutation-types/common'

export default {
  showMsg ({ commit }, o) {
    commit(commontypes.SET_POPMESSAGE, o)
    setTimeout(() => {
      commit(commontypes.CLEAR_POPSHOW)
      setTimeout(() => commit(commontypes.CLEAR_POPMESSAGE), 2000)
    }, 2000)
  },
  passwordFun ({ commit }, event) {
    let id = '#' + event.srcElement.id.replace('ShowHide', '').replace('Delete', '')
    let el = document.querySelector(id)
    if (el.type === 'password') {
      el.type = 'text'
    } else {
      el.type = 'password'
    }
  }
}



