import * as types from './mutation-types/common'

export default {
  [types.AUTH_FAILURE] (state, payload) {
    if (payload.message && payload.message.indexOf('Cannot read property') !== -1) {
      return
    }
    document.querySelector('#common-message').style.display = 'block'
    state.popMessage = payload.message || '服务器繁忙，请稍后重试'
    setTimeout(() => {
      document.querySelector('#common-message').style.display = 'none'
    }, 2000)
  },
  [types.AUTH_USERINFO] (state, payload) {
    state.userInfo = payload.data.user
  },
  [types.AUTH_ISLOGIN] (state) {
    state.isAuth = true
  },
  [types.AUTH_CLEARUSERINFO] (state) {
    state.userInfo = {}
    state.isAuth = false
  },
  [types.SET_POPMESSAGE] (state, payload) {
    state.popShow = true
    state.popMessage = payload.message
  },
  [types.CLEAR_POPSHOW] (state) {
    state.popShow = false
  },
  [types.CLEAR_POPMESSAGE] (state) {
    state.popMessage = ''
  },
  [types.RECEIVE_SMSCODESEQ] (state, payload) {
    if (payload.data) {
      sessionStorage.setItem('smsCodeSeq', payload.data.codeSeq)
    } else {
      sessionStorage.setItem('smsCodeSeq', payload.status)
    }
  },
  [types.SET_SMSCODE] (state, payload) {
    state.smsCode = payload.smsCode
  },
  [types.CLEAR_SMSCODE] (state) {
    state.smsCode = ''
  },
  [types.OPEN_GF_ACCOUNT] (state) {
    state.isOpenGfAccount = true
  }
}



